.main {
  /* margin-top: 4rem; */
  height: 100%;
  font-family: Verdana, shabnam, Tahoma, sans-serif;
  overflow: auto;
}
.yechisection{
  background-color: #1e88e5;
  height: 20px;
}
.plansection{
  background-color: #f6f6f6;
  display: block;
  /* background-color:green;  */
}
.plancontainer {
  margin: 0 auto;
  max-width: 1400px;
  padding: 20px 20px;
  box-sizing: border-box;
 justify-content: center;
  display: flex; 
  flex-wrap: wrap;
  flex-direction: row;
}

.plan {
  background-color: #ffffff;
text-align: center;
width: 80%;
  border-radius: 15px;
  margin:10px 0.5rem;
  box-shadow: 0 5px 15px 0 rgb(110 110 110 / 10%);
  /* font-weight:normal; */
}
.plan ul{
  margin: 0;
}
.planheader {
  padding: 20px 30px;font-weight: bold;
}
.plandetails {
  list-style: none; 
  background-color: #1e88e5;
  text-align: center;
  color: white;
  padding: 10px 30px;
  font-size: 16px;
}
.plandetails li{
  padding-top: 2rem;
}
.planfooter {
  padding: 1rem;
}

@media only screen and (min-width: 576px) {
 
 .plan{
  width: 15rem;
 }
 
}
